import React from 'react';
import { Clock } from 'react-feather'

type ResultCardProps = {
    img: any;
    title: String;
    author: String;
    time: String;
    buttonText: String;
    showDetail: any
}

const ResultCard: React.FC<ResultCardProps> = (props: ResultCardProps) => {

    const { img, title, author, time, buttonText, showDetail } = props

    return (
        <div className="result-card">
            <div className="result-card__img">{img}</div>
            <div>
                <p className="result-card__title">{title}</p>
                <p className="result-card__author">{author}</p>
            </div>

            <div className="result-card__actions">
                <button type="button"
                    onClick={() => showDetail(title, author, img)}
                >
                    {buttonText}
                </button>
                <div className="d-flex-center">
                    <Clock size={16} color="#8C88C4" />
                    <p className="time-text">{time}</p>
                </div>
            </div>
        </div>
    )
};

export default ResultCard;
