import React, { useState, useMemo } from 'react';
import { AlignJustify, X } from 'react-feather'
import logo from '../../assets/images/icons/Innential-logo.png';

const Header: React.FC = () => {

  const [expand, setExpand] = useState(false)

  const expandMenuMemo = useMemo(() => (
    expand &&
    <div className="mobile-expand-header">
      <div className="mobile-expand-header__action d-flex-center justify-between">
        <img src={logo} alt="logo" className="img-logo" />
        <div
          className="close-btn"
          role="button"
          tabIndex={0}
          onClick={() => setExpand(false)}
          onKeyDown={() => setExpand(false)}>
          <X size={28} color="#5A55AB" />
        </div>
      </div>
      <div className="mobile-expand-header__content header-links">
        <p className="header-links__item">Virtual Library</p>
        <p className="header-links__item">Events</p>
        <p className="header-links__item">Community</p>
        <p className="header-links__item">Insights</p>
        <p className="header-links__item">Login</p>
        <p className="header-links__item">Sign Up</p>
      </div>
    </div>
  ), [expand, setExpand])

  return (
    <div>
      <header className="header d-flex-center justify-between">
        <div className="header__menu d-flex-center">
          <img className="header__logo" src={logo} alt="logo" />
          <div className="header-links d-flex-center">
            <p className="header-links__item">Virtual Library</p>
            <p className="header-links__item">Events</p>
            <p className="header-links__item">Community</p>
            <p className="header-links__item">Insights</p>
          </div>
        </div>
        <div className="header__action d-flex-center">
          <p className="header__btn-login header__action--btn">Login</p>
          <p className="header__btn-signup header__action--btn">Sign Up</p>
        </div>
      </header>

      <header className="mobile-header justify-between">
        <img className="mobile-header__logo img-logo" src={logo} alt="logo" />
        <div className="mobile-header__expand-btn"
          role="button"
          tabIndex={0}
          onClick={() => setExpand(true)}
          onKeyDown={() => setExpand(true)}>
          <AlignJustify color="#5A55AB" />
        </div>
      </header>

      {expandMenuMemo}
    </div>
  )
}

export default Header;
