import React from 'react';
import TabModel from '../../utils/models/TabModel';

type TabCardProps = {
    tab: TabModel;
    select: boolean;
    onSelect: any;
}

const TabCard: React.FC<TabCardProps> = (props: TabCardProps) => {

    const { tab, select, onSelect } = props

    return (
        <div className={select ? "card-tab select" : "card-tab"}
            role="button"
            tabIndex={tab.id}
            onClick={() => onSelect(tab.id)}
            onKeyDown={() => onSelect(tab.id)}
            style={{
                borderLeft: `7px solid ${tab.color}`,
                backgroundColor: select ? `${tab.color}` : '#fff'
            }}>

            <div className="d-flex-center justify-between">
                <p className="card-tab__title"><b>{tab.title}</b></p>
                {tab.icon}
            </div>

            <p className="card-tab__value">{tab.value}</p>
        </div>
    )

};

export default TabCard;
