import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { useDispatch } from 'react-redux';

import 'react-multi-carousel/lib/styles.css';

import DefaultLayout from '../../assets/layout/DefaultLayout';

import Tabs from '../../components/Tabs';
import MainContent from '../../components/MainContent';
import FilterSidebar from '../../components/FilterSidebar';
import DetailModal from '../../components/DetailModal';

const Home: React.FC = () => {
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);
  const [selectTab, setSelectTab] = useState(-1);

  const [title, setTitle] = useState('');
  const [author, setAuthor] = useState('');
  const [img, setImg] = useState();

  useEffect(() => {

  }, [dispatch]);

  const toggle = useCallback(() => {
    setOpen(!open)
  }, [setOpen, open])


  const handleTabSelection = useCallback((id) => {
    if (id === selectTab) {
      setSelectTab(-1)
    } else {
      setSelectTab(id)
    }
  }, [setSelectTab, selectTab])

  const showDetail = useCallback((dTitle, dAuthor, dImg) => {
    setTitle(dTitle)
    setAuthor(dAuthor)
    setImg(dImg)
    setOpen(true)
  }, [setOpen, setTitle, setAuthor, setImg])

  const detailModalMemo = useMemo(() => (
    <DetailModal
      open={open}
      title={title}
      author={author}
      img={img}
      toggle={toggle} />
  ), [title, author, img, open])

  return (
    <DefaultLayout>
      <Tabs selectTab={selectTab} handleTabSelection={handleTabSelection} />
      <div className="content-layout">
        <section className="content-layout__sidebar">
          <FilterSidebar />
        </section>
        <section className="content-layout__body">
          <MainContent selectTab={selectTab} showDetail={showDetail} />
        </section>
        <section className="content-layout__modal">
          {detailModalMemo}
        </section>
      </div>
    </DefaultLayout>
  );
};

export default Home;
