import React from 'react';
import { Clock } from 'react-feather'

type RecommendCardProps = {
    img: any;
    title: String;
    content: String;
    time: String;
}

const RecommendCard: React.FC<RecommendCardProps> = (props: RecommendCardProps) => {

    const { img, title, content, time } = props

    return (
        <div className="recommend-card">
            <div className="recommend-card__img">{img}</div>
            <div>
                <div className="recommend-card__path">
                    LEARNING PATH
                </div>
                <p className="recommend-card__title">{title}</p>
                <p className="recommend-card__content">{content}</p>
            </div>

            <div className="recommend-card__actions d-flex-center">
                <Clock size={16} color="#8C88C4" />
                <p className="time-text">{time}</p>
            </div>
        </div>
    )

};

export default RecommendCard;
