import React, { useEffect, useState, useMemo } from 'react';
import { useDispatch } from 'react-redux';
import TabModel from '../../utils/models/TabModel';
import TabCard from '../TabCard';
import TabsMock from '../../utils/resources/tabsMock';

interface TabsProps {
  selectTab: number,
  handleTabSelection: any,
}

const Tabs: React.FC<TabsProps> = (props: TabsProps) => {
  const dispatch = useDispatch();

  const { selectTab, handleTabSelection } = props;

  const [tabs, setTabs] = useState<TabModel[]>([]);

  const tabCardMemo = useMemo(() => {
    const content: any[] = []

    tabs.map((tab, id) => {
      const temp = <TabCard
        key={tab.id}
        tab={tab}
        select={selectTab === id}
        onSelect={handleTabSelection}
      />;
      content.push(temp);
      return true;
    });
    return content
  }, [selectTab, tabs])

  useEffect(() => {
    setTabs(TabsMock.table);
  }, [dispatch]);

  return (
    <div className="tabs">
      {tabCardMemo}
    </div>
  )
};

export default Tabs;
