import React, { useEffect, useMemo, useState } from 'react';
import { useDispatch } from 'react-redux';

import { ChevronRight, ChevronLeft } from 'react-feather';
import Carousel from 'react-multi-carousel';

import ItemsMock from '../../utils/resources/itemsMock';
import ItemModel from '../../utils/models/ItemModel';
import RecommendModel from '../../utils/models/RecommendModel';

import ResultCard from '../ResultCard';
import RecommendCard from '../RecommendCard';

import itunesCategory from '../../assets/images/slider/itunes-category.png';
import blinkistCategory from '../../assets/images/slider/blinkist-category.png';

import { responsive1, responsive2 } from '../../utils/resources/responsive';

const ActionGroup: React.FC<any> = ({ next, previous }) => (
  <div className="action-group d-flex-center">
    <div className="action-group__btn-previous d-flex-center"
      role="button"
      tabIndex={0}
      onClick={() => previous()}
      onKeyDown={() => previous()}
    >
      <ChevronLeft size={18} color="#556685" />Previous
    </div>
    <div className="action-group__btn-next d-flex-center"
      role="button"
      tabIndex={0}
      onClick={() => next()}
      onKeyDown={() => next()}
    >
      More<ChevronRight size={18} color="#556685" />
    </div>
  </div>
)

interface MainContentProps {
  selectTab: number,
  showDetail: any
}

const MainContent: React.FC<MainContentProps> = (props: MainContentProps) => {
  const dispatch = useDispatch();

  const { selectTab, showDetail } = props;

  const [elements, setElements] = useState<any>([]);

  const [books, setBooks] = useState<ItemModel[]>([]);
  const [videos, setVideos] = useState<ItemModel[]>([]);
  const [blinkists, setBlinkists] = useState<ItemModel[]>([]);
  const [freeArticles, setFreeArticles] = useState<ItemModel[]>([]);
  const [podcasts, setPodcasts] = useState<ItemModel[]>([]);
  const [itunes, setItunes] = useState<ItemModel[]>([]);
  const [populars, setPopulars] = useState<ItemModel[]>([]);
  const [likes, setLikes] = useState<ItemModel[]>([]);
  const [recommends, setRecommends] = useState<RecommendModel[]>([]);

  const itemContentMemo = (items: ItemModel[], buttonText: string) => {
    const content: React.ReactElement[] = []

    items.forEach((item: ItemModel) => {
      content.push(
        <ResultCard
          key={item.id}
          img={item.img}
          title={item.title}
          author={item.author}
          time={item.time}
          buttonText={buttonText}
          showDetail={showDetail}
        />
      )
    });

    return content
  };

  const recommendContentMemo = useMemo(() => {
    const content: any[] = []

    recommends.map((item) => {
      content.push(
        <RecommendCard
          key={item.id}
          img={item.img}
          title={item.title}
          content={item.content}
          time={item.time}
        />
      )
      return true
    })
    return content
  }, [recommends])

  const listContentMemo1 = useMemo(() => {
    switch (selectTab) {
      case -1:
        return itemContentMemo(books, "Details")
      case 0:
        return itemContentMemo(blinkists, "Details")
      case 1:
        return itemContentMemo(podcasts, "Listen")
      case 2:
        return itemContentMemo(populars, "Watch")
      default:
        return itemContentMemo(books, "Details")
    }
  }, [selectTab, books]);

  const listContentMemo2 = useMemo(() => {
    switch (selectTab) {
      case -1:
        return itemContentMemo(videos, "Watch")
      case 0:
        return itemContentMemo(freeArticles, "Details")
      case 1:
        return itemContentMemo(itunes, "Listen")
      case 2:
        return itemContentMemo(likes, "Watch")
      case 3:
        return <></>
      default:
        return itemContentMemo(videos, "Watch")
    }
  }, [selectTab, videos]);

  const categoryMemo1 = useMemo(() => {
    switch (selectTab) {
      case -1:
        return <p className="content-list-title">Top-5 Books</p>
      case 0:
        return <img src={blinkistCategory} className="category-list-img" alt="blinkist-category" />
      case 1:
        return <p className="content-list-title">Best from CIPD Podcast</p>
      case 2:
        return <p className="content-list-title">Popular in August</p>
      case 3:
        return <p className="content-list-title">Recommended Paths</p>
      default:
        return <p className="content-list-title">Top-5 Books</p>
    }
  }, [selectTab])

  const categoryMemo2 = useMemo(() => {
    switch (selectTab) {
      case -1:
        return <p className="content-list-title">Top-5 Videos</p>
      case 0:
        return <p className="content-list-title">Free Articles</p>
      case 1:
        return <img src={itunesCategory} className="category-list-img" alt="itunes-category" />
      case 2:
        return <p className="content-list-title">You may also like</p>
      case 3:
        return <></>
      default:
        return <p className="content-list-title">Top-5 Videos</p>
    }
  }, [selectTab])

  const carouselMemo1 = useMemo(() => (
    <Carousel
      arrows={false}
      customButtonGroup={<ActionGroup />}
      containerClass="carouel__container"
      itemClass="carousel__item"
      responsive={responsive1}>
      {listContentMemo1}
    </Carousel >
  ), [selectTab, listContentMemo1])

  const carouselMemo2 = useMemo(() => (
    selectTab !== 3 && <Carousel
      arrows={false}
      customButtonGroup={<ActionGroup />}
      containerClass="carouel__container"
      itemClass="carousel__item"
      responsive={responsive1}>
      {listContentMemo2}
    </Carousel >
  ), [selectTab, listContentMemo2])

  const carouselRecommendMemo = useMemo(() => (
    <Carousel
      arrows={false}
      customButtonGroup={<ActionGroup />}
      containerClass="carouel__container"
      itemClass="carousel__item"
      responsive={responsive2}>
      {recommendContentMemo}
    </Carousel >
  ), [recommendContentMemo])

  useEffect(() => {
    switch (selectTab) {
      case 3:
        setElements([categoryMemo1, carouselRecommendMemo])
        break;
      default:
        setElements([categoryMemo1, carouselMemo1])
        break;
    }

    setBooks(ItemsMock.booksTable);
    setVideos(ItemsMock.videosTable);
    setBlinkists(ItemsMock.blinkistsTable);
    setFreeArticles(ItemsMock.freeArticlesTable);
    setPodcasts(ItemsMock.podcastsTable);
    setItunes(ItemsMock.itunesListTable);
    setPopulars(ItemsMock.popularsTable);
    setLikes(ItemsMock.likesTable);
    setRecommends(ItemsMock.recommendsTable);
  }, [dispatch, selectTab, books]);

  return (
    <>
      <article className="main-content">
        <section className="main-content__title d-flex-center justify-between">
            {elements[0]}
        </section>
        <section className="main-content__list">
            {elements[1]}
        </section>
      </article>

      <article className="main-content">
        <section className="main-content__title d-flex-center justify-between">
            {categoryMemo2}
        </section>
        <section className="main-content__list">
            {carouselMemo2}
        </section>
      </article>
    </>
  )
};

export default MainContent;
